.nutritionArticles__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 15px;
  padding-bottom: 70px; }
  .nutritionArticles__container .importantArticles, .nutritionArticles__container .otherArticles {
    width: 40vw; }
  .nutritionArticles__container .importantArticles {
    margin-left: 20px; }
    .nutritionArticles__container .importantArticles .importantArticle__topic {
      font-size: 30px;
      border: 2px solid black;
      border-left: none;
      border-right: none;
      font-family: Pacifico, cursive;
      text-align: center; }
    .nutritionArticles__container .importantArticles .importantArticle__picture {
      width: 80%;
      height: auto;
      display: block;
      margin: 0 auto; }
    .nutritionArticles__container .importantArticles .importantArticle__text {
      margin-top: 15px;
      border: 1px solid black;
      padding: 8px; }
  .nutritionArticles__container .otherArticles .otherArticles__heading {
    font-size: 30px;
    font-family: Pacifico, cursive; }
  .nutritionArticles__container .otherArticles .otherArticle {
    display: flex;
    flex-direction: row;
    margin-right: 80px;
    margin-bottom: 50px; }
    .nutritionArticles__container .otherArticles .otherArticle:hover {
      color: green; }
    .nutritionArticles__container .otherArticles .otherArticle .otherArticle__picture {
      width: 50%;
      height: 100%;
      margin-right: 0; }
    .nutritionArticles__container .otherArticles .otherArticle .otherArticle__text {
      width: 50%;
      padding: 5px;
      border: 1px solid black;
      border-left: none; }
      .nutritionArticles__container .otherArticles .otherArticle .otherArticle__text a:hover {
        color: green; }
      .nutritionArticles__container .otherArticles .otherArticle .otherArticle__text:hover {
        border: 2px solid green;
        border-left: none;
        padding-right: 0; }

a {
  color: black; }

@media screen and (max-width: 800px) {
  .nutritionArticles__container {
    margin-top: 20px;
    flex-direction: column; }
    .nutritionArticles__container .importantArticles, .nutritionArticles__container .otherArticles {
      width: 80vw; }
    .nutritionArticles__container .importantArticles {
      margin-left: 0;
      margin-right: 60px; }
      .nutritionArticles__container .importantArticles .importantArticle__topic {
        text-align: left; }
    .nutritionArticles__container .importantArticle {
      width: inherit; }
    .nutritionArticles__container .otherArticle {
      width: 80vw;
      margin-right: 10px; }
      .nutritionArticles__container .otherArticle .otherArticle__picture {
        height: auto; } }
