.toggleButton {
  text-align: center; }
  .toggleButton .btn {
    margin-top: 0;
    margin-left: 10px;
    margin-right: 10px; }

.iframe-container {
  overflow: hidden;
  padding-top: 56.25%;
  position: relative;
  width: 38vw; }

.iframe-container iframe {
  border: 0;
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%; }

.iframe-container-4x3 {
  padding-top: 75%; }

.nutrition__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
  padding-bottom: 50px; }
  .nutrition__container .text {
    width: 35vw; }
  .nutrition__container .video {
    margin-left: -18px; }
    .nutrition__container .video .video__name {
      font-size: 23px;
      padding-left: 15px;
      border-top: 1px solid black;
      border-bottom: 1px solid black;
      font-family: Pacifico, cursive;
      width: 38vw; }
  .nutrition__container .text {
    margin-right: 70px;
    -webkit-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    -ms-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    padding: 15px; }
    .nutrition__container .text .important {
      font-weight: 800;
      text-align: center; }
    .nutrition__container .text .btn {
      margin-top: 0; }
  .nutrition__container .photo-sitting {
    width: 10vw;
    position: fixed;
    top: 50%;
    left: 44.5%; }
    .nutrition__container .photo-sitting img {
      width: 100px;
      height: 100px; }

@media screen and (max-width: 750px) {
  .nutrition__container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start; }
    .nutrition__container .video, .nutrition__container .text {
      width: 75vw;
      padding-left: 0;
      margin-right: 0; }
    .nutrition__container .text {
      padding: 15px;
      padding-top: 10px;
      margin-top: 15px; }
    .nutrition__container button {
      float: right;
      padding: 10px 20px; }
  .video__name {
    width: 75vw; }
  .photo-sitting {
    display: none; } }
