nav {
  text-align: center;
  font-family: Pacifico, cursive;
  margin-top: 40px;
  margin-bottom: 50px; }

.menuFruit {
  width: 35px;
  height: auto; }

.menu-flex {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding-bottom: 15px;
  height: 100vh; }

ul {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }
  ul li a {
    color: black;
    cursor: pointer; }
    ul li a:hover {
      color: #CB8B8B; }

.question {
  margin-bottom: -3px;
  font-weight: 900; }

.exitButton {
  float: right; }

.exitButtonImage {
  width: 50px;
  height: 50px; }
  .exitButtonImage:hover {
    transform: scale(1.1); }

.answer {
  font-size: 23px; }

@media screen and (max-width: 600px) {
  nav {
    margin-bottom: 500px; }
  .menu-flex {
    position: relative;
    top: 0;
    bottom: 10;
    left: 0;
    right: 0; } }
