.header {
  display: flex;
  flex-direction: row;
  justify-content: center; }
  .header .logo {
    width: 80px;
    height: auto; }
    .header .logo:hover {
      cursor: pointer; }

.navButtons {
  display: flex;
  flex-direction: column;
  align-content: flex-end;
  position: fixed;
  right: 3vw;
  margin-top: 10px; }

.navButton {
  margin-bottom: 20px;
  width: 3em;
  height: 3em;
  cursor: pointer;
  max-width: 50px; }
  .navButton:hover {
    transform: scale(1.1); }

@media screen and (max-width: 600px) {
  .navButton {
    width: 2.5rem;
    height: 2.5rem; }
  .navButtons {
    right: 5px; } }
