.socials img {
  width: 35px;
  height: 35px;
  cursor: pointer;
  margin-right: 15px;
  opacity: 1; }
  .socials img:hover {
    cursor: pointer;
    transform: scale(1.1); }

.socials {
  display: flex;
  align-items: center; }

.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  background-color: #ffffff;
  text-align: center;
  opacity: 0.9;
  display: flex;
  flex-direction: row;
  justify-content: space-around; }

.developer .love {
  width: 15px;
  height: 15px; }

.developer .copyright {
  width: 15px;
  height: 15px; }

@media screen and (max-width: 600px) {
  .socials {
    width: 100vw; }
  .developer {
    display: none; } }

@media screen and (max-height: 500px) {
  .footer {
    display: none; } }
