.aboutMe__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-top: 25px;
  padding-bottom: 45px;
  font-size: 1.1em; }
  .aboutMe__container .photo {
    width: 250px;
    height: auto;
    opacity: 0.9;
    border-radius: 50px;
    cursor: auto; }
    .aboutMe__container .photo:hover {
      opacity: 1; }
  .aboutMe__container .photoSummary {
    width: 300px; }
  .aboutMe__container .summary {
    padding-top: 20px;
    font-size: 1.2em; }
  .aboutMe__container .info {
    text-align: justify;
    width: 60vw;
    padding-right: 40px; }
    .aboutMe__container .info .info__heading {
      font-weight: bold; }

.particles {
  position: absolute;
  top: 110px;
  bottom: 200px;
  right: 955px;
  left: 145px;
  z-index: 1; }

@media screen and (min-width: 601px) and (max-width: 750px) {
  .particles {
    display: none; }
  .aboutMe__container {
    justify-content: flex-start; }
  .photoSummary {
    display: flex;
    flex-direction: row;
    justify-content: space-between; }
  .summary {
    padding-left: 25px; }
  .info .info__paragraph {
    width: 75vw; }
  .info {
    display: flex;
    flex-direction: column;
    padding-top: 20px; } }

@media screen and (max-width: 600px) {
  .aboutMe__container {
    justify-content: flex-start; }
  .photoSummary {
    width: 75vw; }
  .info {
    padding-right: 0;
    padding-top: 20px; }
  .info .info__paragraph {
    width: 75vw; } }
