.toggleButton {
  margin-top: 10px; }

.motivation__container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 15px;
  padding-bottom: 100px;
  margin-right: 20px;
  width: 82vw;
  font-family: 'Comfortaa', cursive; }
  .motivation__container .video__name {
    font-size: 23px;
    padding-left: 15px;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    font-family: Pacifico, cursive; }
  .motivation__container .iframe-container {
    overflow: hidden;
    padding-top: 56.25%;
    position: relative;
    width: 38vw; }
  .motivation__container .iframe-container iframe {
    border: 0;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%; }
  .motivation__container .iframe-container-4x3 {
    padding-top: 75%; }
  .motivation__container .text {
    -webkit-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    -ms-box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    box-shadow: 4px 4px 31px 1px rgba(65, 255, 84, 0.74);
    margin-top: 20px;
    margin-right: 20px;
    width: 40vw;
    padding: 10px; }
    .motivation__container .text .important {
      font-weight: 800;
      padding-top: 15px;
      padding-bottom: 10px; }
    .motivation__container .text .btn {
      margin-top: 10px;
      margin-left: 10px; }
    .motivation__container .text .new-topic {
      padding-top: 15px; }

@media screen and (max-width: 1100px) {
  .motivation__container {
    flex-direction: column;
    width: 75vw;
    justify-content: center;
    margin: 0 auto;
    margin-left: 10px;
    padding-bottom: 50px;
    margin-right: 0; }
    .motivation__container .video__name {
      text-align: center; }
    .motivation__container .text {
      width: 75vw;
      text-align: justify; }
    .motivation__container .iframe-container {
      width: 75vw; }
  .form-inline {
    padding-bottom: 10px;
    width: 60vw; }
    .form-inline .form-control {
      margin-bottom: 20px;
      margin-left: 10px; }
    .form-inline button {
      padding: 10px 40px; } }
