.homepageSecond {
  margin-top: 150px;
  padding-bottom: 60px;
  font-family: cursive;
  height: 100vh; }

.homepageSecond__heading {
  text-align: center;
  font-size: 1.8em; }
  .homepageSecond__heading div {
    text-transform: uppercase;
    border-bottom: 1px solid black;
    border-top: 1px solid black;
    width: 50%;
    margin: 0 auto; }

.homepageSecond__info {
  text-align: center;
  font-size: 1.2em; }

.homepageSecond__choose {
  text-transform: uppercase;
  border: 1px solid black;
  width: 50vw;
  margin: 0 auto;
  font-weight: bold; }

@media screen and (max-width: 600px) {
  .homepageSecond {
    font-family: sans-serif;
    margin-bottom: 20px; } }
