.contacts__container {
  margin-right: 0; }
  .contacts__container .contact-input {
    display: flex;
    flex-wrap: wrap;
    padding-top: 20px; }
  .contacts__container .contacts__text {
    width: 50vw;
    text-align: center;
    margin: 0 auto;
    margin-top: 10px; }
  .contacts__container form {
    margin-top: 40px;
    width: 40vw;
    margin: 0 auto; }
    .contacts__container form .form-group {
      margin-left: 25px; }
    .contacts__container form .button {
      display: block;
      margin: 0 auto;
      margin-top: 30px; }
      .contacts__container form .button:hover {
        color: black;
        background-color: #41ff54; }

.submit-button {
  display: block;
  margin: auto;
  margin-top: 20px;
  background-color: #41ff54;
  color: black; }

@media screen and (max-width: 600px) {
  .form {
    margin-bottom: 20px; } }
